@import url(https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{font-size:62.5%}*{margin:0;padding:0;box-sizing:border-box}

.Header{width:100%;height:70vh;margin-bottom:12rem}@media screen and (max-width: 54.6875em){.Header{height:100vh}}.Header .img-box{display:inline-block;background-image:url(/static/media/image-hero.3de81110.jpg);width:100%;height:100%;background-position:center;background-size:cover;background-repeat:no-repeat}@media screen and (max-width: 54.6875em){.Header .img-box{background-image:url(/static/media/image-hero.9b5aacea.jpg)}}@media screen and (max-width: 54.6875em){.Header .Tile-container{display:flex;align-items:center}}.Header .title{color:white;font-size:5.2rem;border:2px solid white;font-weight:300;display:inline-block;margin-left:12.8rem;padding:2rem 10rem 2rem 4rem;line-height:1.1}@media screen and (max-width: 54.6875em){.Header .title{font-size:3.6rem;margin:6.4rem 3rem;padding:2rem 4rem 2rem 2rem}}.Header .title span{display:block;letter-spacing:3px}

.navbar{font-family:"Alata", sans-serif;margin:6.4rem 12.8rem;align-items:center;justify-content:space-between;color:#fff;font-size:1.5rem;display:flex}@media screen and (max-width: 54.6875em){.navbar{margin:auto;margin:3.4rem 3rem;font-size:1rem}}.navbar .links{display:flex;grid-gap:2rem;gap:2rem}@media screen and (max-width: 54.6875em){.navbar .links{display:none}}.navbar .links .link{color:inherit;list-style:none;text-decoration:none;position:relative}.navbar .links .link:hover::after{content:"";position:absolute;width:100%;height:0.175rem;background-color:white;left:0;bottom:-4px}.navbar .menu{cursor:pointer}.navbar .closeMenu{z-index:1;margin-left:-50vw;left:50%;position:fixed;height:100vh;width:100vw;top:0;background-color:#000;display:flex;border:5x solid red;flex-direction:column}.navbar .closeMenu .logo-and-Menu{margin:3.4rem 3rem;margin-bottom:12rem;display:flex;justify-content:space-between}.navbar .closeMenu .logo-and-Menu .close-img{width:2.4rem}.navbar .closeMenu .menuLinks{font-family:"Josefin Sans", sans-serif;margin-left:3rem;font-size:3.6rem;display:flex;flex-direction:column;width:100%}.navbar .closeMenu .menuLinks .link{text-decoration:none;color:#fff;margin-bottom:2rem}

.navbar{font-family:"Alata", sans-serif;margin:6.4rem 12.8rem;align-items:center;justify-content:space-between;color:#fff;font-size:1.5rem}@media screen and (max-width: 54.6875em){.navbar{margin:auto;margin:3.4rem 3rem;font-size:1rem}}.navbar .logo{z-index:99}@media screen and (max-width: 54.6875em){.navbar .logo{width:15rem}.navbar .logo img{width:100%}}

.mainSection{margin:auto 0}@media screen and (max-width: 54.6875em){.mainSection .btn-container{display:flex;justify-content:center;margin-top:-12rem;margin-bottom:8rem}}

.intractiveSection{margin:0rem 12.8rem;margin-bottom:16rem;font-size:1.3rem}@media screen and (max-width: 77.5em){.intractiveSection{margin:16rem auto;justify-content:center;width:60rem;display:grid;grid-template-columns:1fr}}@media screen and (max-width: 54.6875em){.intractiveSection{width:32rem}}.intractiveSection-img-container{position:relative}@media screen and (max-width: 77.5em){.intractiveSection-img-container{position:static}.intractiveSection-img-container img{width:100%;margin-bottom:6rem}}.intractiveSection-img-container-img{z-index:-1}.intractiveSection-text{font-family:"Alata", sans-serif;position:absolute;background-color:#fff;z-index:1;bottom:0rem;left:50rem;padding-top:9.6rem;padding-left:9.6rem}@media screen and (max-width: 77.5em){.intractiveSection-text{display:flex;flex-direction:column;position:static;padding:0;text-align:center}}.intractiveSection-text .h2{font-family:"Josefin Sans", sans-serif;font-size:4.6rem;margin-bottom:2.4rem}@media screen and (max-width: 54.6875em){.intractiveSection-text .h2{font-size:3rem}}.intractiveSection-text p{color:#8c8c8c;line-height:1.6;font-size:1.4rem;font-weight:300}@media screen and (max-width: 77.5em){.intractiveSection-text p{max-width:35ch;margin:auto}}

.CreationSection{margin:0 12.8rem;margin-bottom:8rem;display:flex;justify-content:space-between}@media screen and (max-width: 54.6875em){.CreationSection{display:flex;justify-content:center;font-size:2.4rem;margin:0 0;margin-bottom:4rem}}.CreationSection .h2{font-family:"Josefin Sans", sans-serif;font-size:4.6rem;color:#000}@media screen and (max-width: 54.6875em){.CreationSection .h2{font-size:3.2rem}}@media screen and (max-width: 54.6875em){.CreationSection .btn{display:none}}

.btn-container .btn{font-family:"Alata", sans-serif;padding:1rem 4rem;letter-spacing:0.5rem;font-size:1.5rem;border:2px solid #000;border:2px solid dimgray}.btn-container .btn:hover{background-color:#000;color:#fff;border:none;transition:0.5s;border:2px solid transparent;cursor:pointer}

.GallarySection{display:grid;grid-template-columns:repeat(4, 1fr);justify-content:space-between;align-items:center;margin:0 12.5rem;margin-bottom:16rem;grid-gap:2.4rem;gap:2.4rem}@media screen and (max-width: 54.6875em){.GallarySection{display:flex;flex-direction:column}}@media screen and (max-width: 77.5em){.GallarySection{grid-template-columns:repeat(2, 1fr);justify-items:center}}.GallarySection .cart{font-family:"Josefin Sans", sans-serif;width:23.5rem;position:relative;cursor:pointer;overflow:hidden}@media screen and (max-width: 77.5em){.GallarySection .cart{min-width:32rem}}.GallarySection .cart::after{content:"";background-color:black;opacity:1}.GallarySection .cart img{width:100%;height:100%;background-size:cover;background-position:center;z-index:-1}.GallarySection .cart img:hover{opacity:0.5;transition:3s;transform:scale(1.2)}.GallarySection .cart .title{color:#fff;width:12.8rem;font-size:3rem;font-size:2.4rem;position:absolute;bottom:4rem;left:2rem}

.Footer{display:grid;grid-template-columns:1fr 1fr;background-color:#000}@media screen and (max-width: 54.6875em){.Footer{display:flex;flex-direction:column}}.Footer .navbar{margin:0;margin-left:12.8rem;padding:4rem 0;display:grid;grid-template-columns:1fr}@media screen and (max-width: 54.6875em){.Footer .navbar{display:flex;align-items:center;flex-direction:column;margin:auto;margin-bottom:-3rem}}.Footer .navbar .logo{width:12.8rem;margin-bottom:2rem}.Footer .navbar .logo img{width:100%}.Footer .navbar .links{font-size:1.3rem}@media screen and (max-width: 54.6875em){.Footer .navbar .links{display:flex;flex-direction:column;align-items:center}}@media screen and (max-width: 54.6875em){.Footer .navbar .links .link{display:block}}.Footer .navbar .links .link:hover::after{content:"";position:absolute;width:100%;height:0.175rem;height:0.25rem;background-color:white;left:0;bottom:-4px}.Footer-right-section{padding:4rem 0;margin-right:12.8rem;display:grid;grid-template-columns:1fr;align-content:start;text-align:right}@media screen and (max-width: 54.6875em){.Footer-right-section{display:flex;align-items:center;flex-direction:column;margin:auto}}.Footer-right-section .social-links{display:flex;grid-gap:2rem;gap:2rem;margin-bottom:2rem;justify-content:right}.Footer-right-section .social-links .img-box{cursor:pointer;border-bottom:2px solid transparent;position:relative;transition:3s}.Footer-right-section .social-links .img-box:hover::after{content:"";position:absolute;width:100%;height:0.175rem;background-color:white;left:0;bottom:-4px}.Footer-right-section .copyright{color:dimgray;font-family:"Alata", sans-serif;font-size:1.3rem}.Footer .menu{cursor:pointer;display:none}

